<script>
import axios from 'axios';
import { API_URL } from '^@/API';
import { globalEvent } from '^@/event';
import _ from 'lodash';
import marker1 from '*@/icon_loca01.png'
import marker2 from '*@/icon_loca02.png'
import marker3 from '*@/icon_loca03.png'
import marker4 from '*@/icon_loca04.png'
import marker5 from '*@/icon_loca05.png'

export default {
  name: 'Sales',
  data: () => ({
    map: null,
    mapClass: false,
    paging: {
      page:1,
      length: 5,
      totalCount: null
    },
    sorting: [
      {
        color: 'green',
        icon: 'insert_emoticon',
        text: '100 ',
      },
      {
        color: 'yellow',
        icon: 'sentiment_satisfied_alt',
        text: '100 ',
      },
      {
        color: 'red',
        icon: 'sentiment_dissatisfied',
        text: '30 ',
      },
      {
        color: 'grey',
        icon: 'sentiment_very_dissatisfied',
        text: '1 ',
      }
    ],
    sortList: [],
    addTitle: null,
    addressSel: ['', ' ', ' ', ' ', ' ', ' ', ' ', '  ', '', '', '', '', '', '', '', '','','','','','',''],
    addressSelChoice:'',
    // 
    address1: [' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' '],
    // 
    address2: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
    //  
    address17: ['  ','  ','  '],
    //  
    address18: ['  ','  ','  '],
    //  
    address19: ['  ','  ','  ','  '],
    //  
    address20: ['  ','  ','  '],
    //  
    address21: ['  ','  '],
    //  
    address22: ['  ','  '],
    // 
    address3: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
    // 
    address4: [' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' '],
    // 
    address5: [' ',' ',' ',' ',' ',' ',' ',' ',' ','  ','  ','  ','  ',' '],
    // 
    address6: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ','  ','  ',' ',' ',' '],
    // 
    address7: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
    // 
    address8: [' ',' ',' ',' ',' '],
    // 
    address9: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ','  ','  '],
    // 
    address10: [' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', '  ', '  ', '  ', '  ', '  ', ' ', ' ', ' ', ' ', ' '],
    // 
    address11: [' ',' ',' ',' ',' ',' ',' ',' '],
    // 
    address12: [' ',' ',' ',' ',' '],
    // 
    address13: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ','  ','  ',' ',' '],
    // 
    address14: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
    // 
    address15: [' ',' ',' ',' ',' '],
    // 
    address16: [' ',' '],
    stores: null
  }),
  methods: {
    getSelected(){
      this.stores = null
      this.addTitle = null;
      this.mapClass = false;
    },
    getStoreBy(add){
      globalEvent.$emit('updateLoader', true);
      this.sortList = [];
      this.mapClass = false;
      const storeList = document.querySelector('.store-list');
      if(storeList) storeList.classList = 'data-list store-list';
      this.addTitle = add;
      const params = {
        address: add,
      }
      axios.get(API_URL+'/storesByAddr/json', {params: params})
        .then(res => {
          this.stores = res.data.stores;
          this.paging.page = parseInt(res.data.page);
          this.paging.totalCount = res.data.totalCount;
          this.paging.length = res.data.totalPages;
          globalEvent.$emit('updateLoader', false);
        })
    },
    getColor(color){
      if(color == 'plenty'){
        return 'green'
      }else if(color == 'some'){
        return 'yellow'
      }else if(color == 'few'){
        return 'red'
      }else if(color == 'empty'){
        return 'grey'
      }
    },
    getGoogleMap(lat, lng){
      window.open(`https://www.google.com/maps?q=${lat},${lng}&ll=${lat},${lng}&z=18`)
    },
    getSortList(stat){
      this.sortList = [];
      const storeList = document.querySelector('.store-list');
      storeList.classList = 'data-list store-list hide'
      _.map(this.stores, (store)=>{
        if(store.remain_stat == stat) {
          this.sortList.push(store)
        }
      })
    },
    getMap(){
      this.mapClass = true;
      var stores = this.stores;
      var map = this.map;
      var kakaomaps = window.kakao.maps;
      var mapContainer = document.getElementById('map'), //   div  
          mapOption = { 
            center: new kakaomaps.LatLng(stores[0].lat, stores[0].lng), //  
            level: 6 //   
      };
      map = new kakaomaps.Map(mapContainer, mapOption); //  
      var geocoder = new kakaomaps.services.Geocoder();
      geocoder.addressSearch(this.addTitle, (result, status)=> {
        if (status === kakaomaps.services.Status.OK) {
          var coords = new kakaomaps.LatLng(result[0].y, result[0].x);
          map.setCenter(coords);
        }
      })
      var imageSize = new kakaomaps.Size(20, 28);
      for (var i = 0; i < stores.length; i ++) {
        var imageSrc;
        var maskState;
        if(stores[i].remain_stat == 'plenty'){
          imageSrc = marker1;
          maskState = '100'
        }else if(stores[i].remain_stat == 'some'){
          imageSrc = marker2;
          maskState = '30'
        }else if(stores[i].remain_stat == 'few'){
          imageSrc = marker3;
          maskState = '30'
        }else if(stores[i].remain_stat == 'empty'){
          imageSrc = marker4;
          maskState = '1'
        }else {
          imageSrc = marker5;
          maskState = ''
        }
        var marker = new kakaomaps.Marker({
          map: map, //   
          position: new kakaomaps.LatLng(stores[i].lat, stores[i].lng),
          image: new kakaomaps.MarkerImage(imageSrc, imageSize)
        });
        marker.setMap(map);
        var infowindow = new kakaomaps.InfoWindow({
          content: `<div class="overline font-weight-medium" style="width:auto; padding:5px; border:none;">${stores[i].name}<span class="overline" style="color:#999; font-size:11px;">(${maskState})</span></div>`
        });
        kakaomaps.event.addListener(marker, 'mouseover', this.markerOver(map, marker, infowindow));
        kakaomaps.event.addListener(marker, 'mouseout', this.markerClose(infowindow));
      }
    },
    markerOver(map, marker, infowindow){
      return ()=> {
        infowindow.open(map, marker);
      }
    },
    markerClose(infowindow){
      return ()=> {
        infowindow.close();
      }
    }
  }
}
</script>

